<template>
  <v-app>
    <v-main class="pt-12">
      <v-snackbar v-model="snackStatue" top :color="snackColor">
        <span>{{ snackText }}</span>
        <template v-slot:action="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            @click="snackStatue = false"
            v-on="on"
            text
            small
            ><v-icon>mdi-close</v-icon></v-btn
          >
        </template>
      </v-snackbar>
      <v-container class="pt-12 mt-16">
        <v-row>
          <v-col>
            <v-card max-width="400" class="mx-auto">
              <v-card-title> تسجيل الدخول </v-card-title>
              <v-card-text>
                <v-divider></v-divider>
                <v-form
                  ref="form"
                  action="/about"
                  id="nativeForm"
                  @submit.prevent="login"
                >
                  <v-card-text class="pb-0">
                    <v-text-field
                      v-model="email"
                      name="email"
                      :rules="[rules.required, rules.email]"
                      label="الايميل"
                      outlined
                    ></v-text-field>
                    <v-text-field
                      v-model="password"
                      name="password"
                      :append-icon="typeText ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.length]"
                      @click:append="typeText = !typeText"
                      label="الباسورد"
                      :type="typeText ? 'text' : 'password'"
                      outlined
                    ></v-text-field>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn
                      type="submit"
                      block
                      :loading="loading"
                      x-large
                      color="primary"
                    >
                      <span class="text-capitalize">تسجيل</span>
                    </v-btn>
                  </v-card-actions>
                </v-form>
              </v-card-text>
              <v-divider></v-divider>
              <!-- <v-card-actions class="justify-center pb-5">
                <router-link style="text-decoration: none" to="/login">
                  <span class="primary--text">forgotten password</span>
                </router-link>
              </v-card-actions> -->
            </v-card>
            <!-- <h1 class="error--text">{{ user }}</h1> -->
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
/* eslint-disable no-unused-vars */
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { mapActions, mapState } from "vuex";
export default {
  data: () => ({
    firstName: "",
    typeText: false,
    lastName: "",
    snackStatue: false,
    snackColor: "error",
    snackText: "المستدم غير صحيح",
    email: "",
    loading: false,
    password: "",
    user: "",
    rules: {
      required: (v) => !!v || "required",
      length: (v) =>
        (v.length > 3 && v.length < 30) || "more than 3 and less than 30",
      email: (v) =>
        /\b\w{1,30}\b@\b\w{1,15}\b\.\b\w{1,8}\b/.test(v) || "invalide email",
    },
  }),
  methods: {
    login() {
      let nativeForm = document.getElementById("nativeForm");
      if (this.$refs.form.validate()) {
        /** */
        // eslint-disable-next-line no-unused-vars
        const auth = getAuth();
        signInWithEmailAndPassword(auth, this.email, this.password)
          .then((userCredential) => {
            // Signed in
            this.user = userCredential.user;
            if (this.user) {
              this.loading = true;
              setTimeout(() => {
                this.snackStatue = true;
                this.snackColor = "success";
                this.snackText = "مرحبا بك";
                this.loading = false;
                this.A_isUserIn(true);
              }, 1500);
              setTimeout(() => {
                this.$router.push({ path: "/admin/welcome" });
                // console.log("welcome admin");
              }, 2000);
            } else {
              console.log("error");
            }
            // ...
          })
          .catch((error) => {
            this.snackStatue = true;
            this.snackColor = "error";
            this.snackText = "المستدم غير صحيح";
            const errorCode = error.code;
            const errorMessage = error.message;
          });
      }
    },
    /*START */
    ...mapActions(["A_isUserIn"]),
  },
  computed: {
    ...mapState({
      isUserIn: (store) => store.port.isUserIn,
    }),
  },
};
</script>

<style lang="scss" scoped></style>
